@import '@@/styles/consts.scss';

.chatHeader {
  width: 100%;
  height: 100%;
  border-bottom: 1px solid $borderGrey;

  display: flex;
  justify-content: flex-start;
  align-items: center;

  &__leftPart {
    flex: 1.5;
    font-size: 6vw !important;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__rightPart {
    flex: 10;
    display: flex;

    align-items: center;
    margin-left: 1vw;
    width: 100%;
    height: 100%
;

    &_partnerName {
      font-size: 5vw !important;
    }

    &_lastSeenDate {
      margin-left: 1vw;
      color: $darkGrey
    }

  }

  // background-color: $grey;
}