@import '@@/styles/consts';

@mixin tabsPositions ($flex) {
  display: flex;
  flex-direction: column;
  flex: $flex;
  height: 90vh;
  & > section:nth-child(1) {
    flex: 0.6
  }
  & > section:nth-child(2) {
    flex: 12
  }

  margin-bottom: 5vh;
}

.messangerPage {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  &_header {
    width: 100%;
    display: flex;
    min-height: 14vw;
    &__return { 
      flex: 1.5;
    }

    &__search {
      flex: 10;
      margin: 2vw;
    }
  }

  &_dialogs {
    width: 100%;
    height: inherit;
  }
