.mePage {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  &__header {
    width: 100%;
    display: flex;
    min-height: 14vw;
  }

  &__wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: auto;

    &_editor {
      width: 100%;
      height: 100%;
      display: flex;
    }
  }

  

}