.chatPage {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;


  &-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &_header {
    width: 100%;
    display: flex;
    min-height: 14vw;
  }

  &_messages {
    width: 100%;
  }

  &_input {
    width: 100%;;
    min-height: fit-content
  }
}