@import '@@/styles/consts';

.previewPage {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  &_title {
    font-size: 7vw !important;
    color: $darkBlue;
    font-style: oblique; 
    margin-bottom: 4vw;
    margin-top: 2vw;
  }

  &_main {
    width: 70vw;
    height: fit-content;
    display: flex;
    flex-direction: column;
    button {
      margin-top: 4vw;
    }
  }
  
}