@import '@@/styles/consts.scss';

.meHeader {
  width: 100%;
  height: 100%;
  border-bottom: 1px solid $borderGrey;

  display: flex;
  justify-content: flex-start;
  align-items: center;

  &__leftPart {
    flex: 1.5;
    font-size: 6vw !important;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__rightPart {
    flex: 10;
    display: flex;
    justify-content: space-between;

    align-items: center;
    margin-left: 1vw;
    width: 100%;
    height: 100%;

    &_label {
      font-size: 5vw !important;
    }

    &_reset {
      margin-right: 2vw;
    }
  }
}