@import '@@/styles/consts.scss';

.editor {
  width: 100%;
  height: fit-content ;
  // border: 1px solid $borderGrey;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  &__main {
    width: 100%;
    margin: 4vw;
    display: flex;
    flex-direction: column;

    &_inputs {
      width: 85%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
   
      &_wrapper {
        margin-bottom: 4vw;

        .addone {
          font-size: 4vw !important;
        }
      }
    }
    
    &_buttons {
      margin-top: 3vw;
      width: 85%;
      display: flex;
      & > button:nth-child(1) {
        margin-right: 3vw;
      }
    }
  }

  &__state {
    width: 20%;
    margin: 2vh;
    display: flex;
    flex-direction: column;

    &_unsaved {
      color: $darkGrey;
      li {
        margin-left: 2vh;
      }
    }

    &_reset {
      margin-bottom: 1vh;
    }
  }
}