  //   max-height: fit-content;
  //   align-items: flex-end;
  //   &_textArea {
  //     flex:12;
  //   }
  //   &_button {
  //     flex: 3;
  //     height: 100%;
  //   }
  // }

  &__main {
    width: 100%;
    height: inherit;
    overflow-x: hidden;
    overflow-y: auto;

    &_wrapper {
      width: 100%;
      height: fit-content;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      &-help {
        justify-content: center;
        align-items: center;
        height: 100%;
